<template>
  <div class="work">
    <!-- 头部 -->
    <van-nav-bar title="上海同望信息技术有限公司" fixed />
    <!-- 轮播 -->
    <div class="conent">
      <van-swipe :autoplay="3000" indicator-color="white" :height="120">
        <van-swipe-item>
          <img src="../../assets/swiper.jpg" alt />
        </van-swipe-item>
        <van-swipe-item>
          <img src="../../assets/swiper.jpg" alt />
        </van-swipe-item>
        <van-swipe-item>
          <img src="../../assets/swiper.jpg" alt />
        </van-swipe-item>
      </van-swipe>
      <!-- 宫格 -->
      <van-grid :column-num="3">
        <van-grid-item icon="home-o" text="待办事项" to="/wait" />
        <van-grid-item icon="photo-o" text="自主检查" to="/self" />
        <van-grid-item icon="apps-o" text="整改" to="/reform" />
        <van-grid-item icon="eye-o" text="复核" to="recheck" />
        <van-grid-item icon="scan" text="签到" to="scan" />
      </van-grid>

      <Attach :attachList="fileList.files" :readonly="false" :uploadFileOption="fileList.type" @uploadCallback="getCallback"></Attach>
    </div>
  </div>
</template>
<script>
import "@/api/request"
import { json_post } from '../../../../../TEST_TEMPLATE/AppVue/hncqProcessHTML5App/src/axios/ajax';
export default {
  components: {
    Attach: () => import("@/components/Attach")
  },
  data() {
    return {
      fileList: {
        files: [],
        type: "SafetyPatrol" // 安全
      },
      fileid:""
    };
  },
  methods: {
     getCallback(data){
        data=eval('(' + data + ')');
        console.log(JSON.stringify(data));
        this.fileid += data.obj + ',';
        console.log(JSON.stringify(this.fileid))
     }
  }
};
</script>
<style  scoped>
img {
  width: 100%;
  height: 100%;
}
.conent {
  margin-top: 46px;
}
</style>


